import React, { useState } from "react";
import { FilterOption } from "@cvccorp-components/core";
import clsx from "clsx";

const _FilterOption = prop => {
    const { filters, filter, index, onApply, className, height } = prop;
    const [item, setItem] = useState(filter);

    const _onOpen = open => {
        const _item = { ...item };
        _item.open = open;
        setItem(_item);
    };

    const _onClose = () => {
        const _item = { ...item };
        Object.assign(_item, { open: false });
        setItem(_item);
    };

    const _onChange = options => {
        const _item = { ...item };
        _item.options = [...options];
        setItem(_item);
    };

    const _onReset = () => {
        const _item = { ...filter };
        Object.assign(_item, { open: item.open, applied: false });
        _item.options.map(opt => Object.assign(opt, { checked: false }));
        setItem(_item);
        filters[index] = _item;
        onApply(filters);
    };

    const _onApply = () => {
        const _item = { ...item };
        Object.assign(_item, { open: false, applied: true });
        setItem(_item);
        filters[index] = _item;
        onApply(filters);
    };

    return (
        <FilterOption
            filter={filter}
            isMobile={false}
            noModal={false}
            sufix={item.sufix}
            open={item.open}
            items={item.options}
            label={item.label}
            onReset={() => _onReset()}
            onApply={() => _onApply()}
            onOpen={() => _onOpen(true)}
            onClose={() => _onClose()}
            onChange={(itemChange, options) => _onChange(options)}
            className={clsx(className, `FilterOption-item-${index}`)}
            height={height}
        />
    );
};

export default _FilterOption;
